import * as util from '../util';
import { positionX } from './bk';
import { max } from '../../../lodash-lite';
export default position;
var keys = Object.keys;

function position(g) {
  g = util.asNonCompoundGraph(g);
  positionY(g);
  var arrPositionX = positionX(g);
  keys(arrPositionX).forEach(function (key) {
    return g.node(key).x = arrPositionX[key];
  });
}

function positionY(g) {
  var layering = util.buildLayerMatrix(g);
  var rankSep = g.graph().ranksep;
  var prevY = 0;
  layering.forEach(function (layer) {
    var maxHeight = max(layer.map(function (v) {
      return g.node(v).height;
    }));
    layer.forEach(function (v) {
      return g.node(v).y = prevY + maxHeight / 2;
    });
    prevY += maxHeight + rankSep;
  });
}