import { flattenDownDepth } from '../../../lodash-lite';
import * as util from '../util';
export default sort;

function sort(entries, biasRight) {
  var parts = util.partition(entries, function (entry) {
    return 'undefined' !== typeof entry.barycenter;
  });
  var sortable = parts.lhs,
      unsortable = parts.rhs.sort(function (entry) {
    return -entry.i;
  }),
      vs = [],
      sum = 0,
      weight = 0,
      vsIndex = 0;
  sortable.sort(compareWithBias(!!biasRight));
  vsIndex = consumeUnsortable(vs, unsortable, vsIndex);
  sortable.forEach(function (entry) {
    vsIndex += entry.vs.length;
    vs.push(entry.vs);
    sum += entry.barycenter * entry.weight;
    weight += entry.weight;
    vsIndex = consumeUnsortable(vs, unsortable, vsIndex);
  });
  var result = {
    vs: flattenDownDepth(vs, [], 1)
  };

  if (weight) {
    result.barycenter = sum / weight;
    result.weight = weight;
  }

  return result;
}

function consumeUnsortable(vs, unsortable, index) {
  var last;

  while (unsortable.length && (last = unsortable.slice(-1)[0]).i <= index) {
    unsortable.pop();
    vs.push(last.vs);
    index++;
  }

  return index;
}

function compareWithBias(bias) {
  return function (entryV, entryW) {
    if (entryV.barycenter < entryW.barycenter) {
      return -1;
    } else if (entryV.barycenter > entryW.barycenter) {
      return 1;
    }

    return !bias ? entryV.i - entryW.i : entryW.i - entryV.i;
  };
}